import React, { useEffect, useState } from "react";
import axios from "axios";
import Header from "./Header.jsx";
import Footer from "./Footer.jsx";
import Sidebar from "./Sidebar.jsx";
import { Link } from "react-router-dom";

function Home() {
  const [courses, setCourses] = useState([]);

  useEffect(() => {
    const fetchCourses = async () => {
      try {
        // const limit = 8;
        const response = await axios.get(
          `http://api.course-selling.id.vn/api/course?sort=["created_at","desc"]`
        );

        if (response.status !== 200) {
          throw new Error("Failed to fetch courses");
        }

        const coursesData = response.data.courses;

        setCourses(coursesData);
      } catch (error) {
        console.error("Error while fetching courses:", error);
      }
    };

    fetchCourses();
  }, []);

  return (
    <div>
      <div className="container-fluid">
        <Header />
        <div className="row">
          <Sidebar />
          <div className="col-lg-11">
            <div className="Home">
              <div className="container-fluid my-2">
                <div className="row box">
                  <div className="col-lg-6 custom-box">
                    <h2>
                      <a href="/#">Hc HTML CSS cho ngi mi</a>
                    </h2>
                    <p className="text-slide">
                      Hnh thc hc Offline ph hp nu bn mun c hng dn
                      v h tr trc tip ti lp. Gi hc linh hot, ph hp c
                      sinh vin v ngi i lm.
                    </p>
                    <button>Tham gia ngay</button>
                  </div>
                  <div className="col-lg-6">
                    <img
                      className="img-banner"
                      src="https://files.fullstack.edu.vn/f8-prod/banners/20/6308a6bf603a4.png"
                      alt=""
                    />
                  </div>
                </div>
                <div className="custom-text">
                  <span className="text-1">Kha hc Pro</span>
                  <span className="text-2">Mi</span>
                  <span className="btn btn-secondary float-end">
                    <Link to={"/course"} className="text-white">
                      Xem tt c kha hc
                    </Link>
                  </span>
                </div>
                <div className="row">
                  {courses.slice(-8).map((course) => (
                    <div key={course.id} className="col-lg-3 mb-3">
                      <Link to={"/course/" + course.id}>
                        <div>
                          <img
                            className="img-item"
                            src={course.thumbnail}
                            alt={course.name}
                          />
                          <div className="name-course">{course.name}</div>
                          <span className="price">
                            <span className="text-black">Gi kha hc:</span>{" "}
                            {Number(course.price).toLocaleString("vi")} VND
                          </span>
                        </div>
                      </Link>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default Home;
