import { useEffect, useState } from "react";
import Cookies from "js-cookie";
import axios from "axios";
import HeaderAdmin from "./HeaderAdmin";
import SidebarAdmin from "./SidebarAdmin";
import { useParams } from "react-router-dom";

function EditPost() {
  let param = useParams();
  let postId = param.postId;
  const user = JSON.parse(Cookies.get("user"));
  if (!user) {
    alert("Please login");
  }
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [status, setStatus] = useState(1);
  const [thumbnail, setThumbnail] = useState(null);
  const [thumbnailUrl, setThumbnailUrl] = useState("");
  const [errorMessage, setErrorMessage] = useState([]);

  useEffect(() => {
    const fetchPost = async () => {
      try {
        const response = await axios.get(
          "http://api.course-selling.id.vn/api/post/" + postId
        );
        const postData = response.data.data;
        setTitle(postData.title);
        setContent(postData.content);
        setStatus(postData.status);
        setThumbnailUrl(postData.thumbnail);
      } catch (error) {
        console.error("Failed to fetch post data: ", error);
      }
    };

    fetchPost();
  }, [postId]);

  const handleThumbnailChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setThumbnail(file);
        setThumbnailUrl(reader.result); // Cp nht URL ca thumbnail  hin th
      };
      reader.readAsDataURL(file);
    }
  };

  const handleEditPost = async (e) => {
    e.preventDefault();

    try {
      const formData = new FormData();
      formData.append("title", title);
      formData.append("content", content);
      formData.append("status", status);
      if (thumbnail !== null) {
        formData.append("thumbnail", thumbnail);
      }

      const response = await axios.post(
        "http://api.course-selling.id.vn/api/post/edit/" + postId,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data", //upload file
            Authorization: `Bearer ${user.access_token}`,
          },
        }
      );
      if (response.data) {
        alert("Chnh sa bi vit thnh cng!");
        window.location.href = "/admin/list-post";
      }
      const { data } = response.data;
      setErrorMessage(data.message);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div className="Admin">
      <div className="container-fluid">
        <div className="row flex-nowrap">
          <div className="col-auto col-md-3 col-xl-2 px-sm-2 px-0 bg-light">
            <SidebarAdmin page="/" />
          </div>
          <div className="col py-1">
            <HeaderAdmin />

            <div className="custom-border-top">
              <form className="custom-form m-auto" onSubmit={handleEditPost}>
                <div className="custom-div-1">Chnh sa bi vit</div>
                <div className="mb-3">
                  <label className="form-label">Tn bi vit: </label>
                  <input
                    className="form-control"
                    placeholder="Tn bi vit"
                    type="text"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Ni dung: </label>
                  <textarea
                    className="form-control"
                    placeholder="Ni dung ca bi vit"
                    value={content}
                    onChange={(e) => setContent(e.target.value)}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Hnh nh chnh sa: </label>
                  <input
                    className="form-control"
                    type="file"
                    onChange={handleThumbnailChange}
                  />
                  <img
                    src={thumbnailUrl || thumbnail}
                    alt=""
                    width={200}
                    style={{ objectFit: "cover" }}
                  />
                </div>
                <div className="mb-3">
                  <div className="form-label"> Trng thi: </div>
                  <div className="mb-1">
                    <input
                      type="radio"
                      value="1"
                      checked={status === 1}
                      onChange={() => setStatus(1)}
                    />
                    <span className="mx-2">Hin th kha hc</span>
                  </div>
                  <div className="mb-1">
                    <input
                      type="radio"
                      value="0"
                      checked={status === 0}
                      onChange={() => setStatus(0)}
                    />
                    <span className="mx-2">n kha hc</span>
                  </div>
                </div>
                <button className="btn btn-primary" type="submit">
                  Lu chnh sa
                </button>
                {errorMessage && <p style={{ color: "red" }}>{errorMessage}</p>}
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default EditPost;
